* {
  box-sizing: border-box;
  font-size: 14px;
  font-family: sans-serif;
  /* background-color: purple; Set background color to purple */
}


.SliderNavigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#wrapper {
  margin: 0 auto;
  width: 600px;
}

h1 {
  margin: 0;
  font-size: 1.5em;
  text-align: center;
}

.slider {
  position: relative;
  height: 420px;
  outline: 0 solid black;
  width: 600px;
  overflow: hidden;
}
.slides {
  transition: all 800ms cubic-bezier(0.770, 0.000, 0.175, 1.000);
  width: 1000%;
}
.slide {
  position: relative;
  float: left;
  height: 400px;
  width: 600px; /* Set the width to match the image width */
  overflow: hidden; 

  
}
.slide .description {
  position: absolute;
  top: calc(100% - 30px);
  z-index: 3;
  opacity: 0.7; /* Set initial opacity */
  height: 30px;
  width: 600px;
  text-align: center;
  padding: 5px;
  background-color: black;
  color: white;
  transition: all 800ms ease;
}
.slide .description:hover {
  opacity: 0.7;
  transition: all 800ms ease;
}
input[type="radio"] {
  display: none;
}
.prev {
  position: absolute;
  cursor: pointer;
  z-index: 2;
  top: 0;
  left: 0;
  height: 400px;
  width: 50px;
  background-color: white;
  opacity: 0.1;
  transition: all 800ms ease;
}
.prev:hover {
  opacity: 0.5;
  transition: all 800ms ease;
}
.next {
  position: absolute;
  cursor: pointer;
  z-index: 2;
  top: 0;
  right: 0;
  height: 400px;
  width: 50px;
  background-color: white;
  opacity: 0.1;
  transition: all 800ms ease;
}
.next:hover {
  opacity: 0.5;
  transition: all 800ms ease;
}
.slider .nav {
  position: absolute;
  text-align: center;
  z-index: 3;
  top: 400px;
  width: 600px;
}
.paragraph {
  width: 600px;
  margin-bottom: 15px;
}
label.bottom {
  background: #cccccc;
  transition: all 800ms cubic-bezier(0.770, 0.000, 0.175, 1.000);
  display: inline-block;
  cursor: pointer;
  width: 30px;
  height: 10px;
  border-radius: 5px;
}
#slide0:checked ~ .nav > label[for="slide0"],
#slide1:checked ~ .nav > label[for="slide1"],
#slide2:checked ~ .nav > label[for="slide2"],
#slide3:checked ~ .nav > label[for="slide3"],
#slide4:checked ~ .nav > label[for="slide4"],
#slide5:checked ~ .nav > label[for="slide5"],
#slide6:checked ~ .nav > label[for="slide6"],
#slide7:checked ~ .nav > label[for="slide7"],
#slide8:checked ~ .nav > label[for="slide8"],
#slide9:checked ~ .nav > label[for="slide9"] {
  background: #333333;
  transition: all 800ms cubic-bezier(0.770, 0.000, 0.175, 1.000);
}
#slide0:checked ~ .slides { margin-left: 0; }
#slide1:checked ~ .slides { margin-left: -100%; }
#slide2:checked ~ .slides { margin-left: -200%; }
#slide3:checked ~ .slides { margin-left: -300%; }
#slide4:checked ~ .slides { margin-left: -400%; }
#slide5:checked ~ .slides { margin-left: -500%; }
#slide6:checked ~ .slides { margin-left: -600%; }
#slide7:checked ~ .slides { margin-left: -700%; }
#slide8:checked ~ .slides { margin-left: -800%; }
#slide9:checked ~ .slides { margin-left: -900%; }


.slide img {
  width: 100%;
  height: 100%;    
}
